.TodoListItem-virtualized{
    &+&{border-top: 1px solid #dee2e6;}
    &:nth-child(even){background: #f8f9fa;}
}

.TodoListItem{
    padding: 1rem;
    display: flex;
    align-items: center;
    &:nth-child(even){background: #f8f9fa;}
    .checkbox{
        cursor: pointer;
        flex: 1;//차지할수있는영역 모두 차지
        display: flex;
        align-items: center;
        svg{ont-size: 1.4rem;}
        .text{margin-left: 0.5rem; flex: 1;}
        &.checked{
            svg{color:#d7c1fa}
            .text{color: #adb5bd; text-decoration: line-through;}
        }
    }
    .remove{
        display: flex;
        align-items: center;
        font-size: 1.4rem;
        color: #636161;
        cursor: pointer;
        &:hover{color: #d7c1fa;} //&는 자기 자신을 의미 
    }
    .edit{
        display: flex;
        align-items: center;
        font-size: 1.3rem;
        color: #636161;
        cursor: pointer;
        &:hover{color: #d7c1fa;} //&는 자기 자신을 의미 
        margin-right: 1rem;
    }
    & + &{border-top: 3px solid #dee2e6;}
}
